@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap");

body {
    margin: 0;
    font-family: "Source Sans Pro", -apple-system, sans-serif;
    background-color: #121212;
    color: #ffffff;
}
.main__class {
    height: 100vh;
    display: flex;
    background-color: #121212;
}
.home {
    width: 100%;
    display: flex;
    height: 100vh;
}
.fullWidth > label {
    width: 100%;
}
.fullWidth {
    display: flex;
    align-items: center;
}
.fullWidth > button {
    margin-top: 0.8rem;
}
.chats {
    width: 70%;
    display: flex;
}
.chats > div:first-of-type {
    display: flex;
    flex-direction: column;
}
.chats > div {
    width: 50%;
}
.logs {
    width: 30%;
    background-color: red;
}
canvas {
    border-radius: 50%;
}
.hideQR > div > button {
    display: none;
}
.hideQR > input {
    padding-right: 3rem;
}
@media only screen and (max-width: 920px) {
    .logs {
        display: none;
    }
    .chats {
        width: 100%;
    }
    .chats > div {
        width: 100%;
    }
    .main__class > div {
        margin: auto 1rem;
    }
    .hideChat {
        display: none;
    }
    .logsBTN {
        display: none !important;
    }
}

.msg_card_body {
    overflow-y: auto;
    font-size: 0.9rem;
    height: 80vh;
}
.anim {
    transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 0.4s;
}
.msg_cotainer {
    cursor: pointer;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 10px;
    border-radius: 25px;
    background-color: #4e3fce;
    padding: 6px 10px;
    position: relative;
    width: fit-content;
    max-width: 50%;
}
.msg_cotainer_send {
    margin-top: auto;
    cursor: pointer;
    margin-bottom: auto;
    margin-right: 10px;
    border-radius: 25px;
    background-color: #3c3c41;
    padding: 6px 10px;
    position: relative;
    width: fit-content;
    max-width: 50%;
}
.msg_time {
    position: absolute;
    right: 7px;
    bottom: -15px;
    color: rgba(255, 255, 255, 0.5);
    font-size: 10px;
}
.msg_time_send {
    position: absolute;
    left: 7px;
    bottom: -15px;
    color: rgba(255, 255, 255, 0.5);
    font-size: 10px;
}
